body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.messages-card {
    display: flex;
    flex-direction: column;

}

.message-box {
    display: flex;
    align-items: flex-end;
}

.message-box .username {
    font-size: .7rem;
    padding: 2px 5px;
    color: grey;
}

.message-box .message {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 8px;
    padding: .4rem .8rem;
    background: rgb(255, 255, 255);
}

.message-box .message .message-text {
    font-size: .9rem;
}

.message-box .message .message-time {
    font-size: .5rem;
    color: #b5b6b8;
}

.message-box-left .message {
    border-radius: 10px 10px 10px 0;
}

.message-box-right {
    flex-direction: row-reverse;
    margin-left: 2rem;
}

.message-box-right .message {
    background: #2e82d6;
    border-radius: 10px 10px 0px 10px;
    color: #fff;
}

.message-box-right .message .message-time {
    color: #a3cef8;
}

.message-box-right {
    align-self: flex-end;
}

.message-box-right .message-time,
.message-box-right .username,
.message-box-right .username {
    text-align: right;
}
.App {
    display: flex;
    flex-direction: column;
}

.header {
    background-color: #2e82d6;
    color: #FFF;
    text-transform: uppercase;
    padding: 0 1rem;
}

.container {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 2rem 0;
}
